.wrapper {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: var(--default-layout-header-height);
    max-width: 100%;
}

.container {
    width: var(--default-layout-width);
    max-width: 100%;
    padding: 0 var(--default-layout-horizontal-spacer);
    display: flex;
}

.content {
    flex: 1;
    min-height: calc(100vh - var(--default-layout-header-height));
    display: flex;
    align-items: center;
    justify-content: center;
}
